<template>
    <div class="screen">
        <Title :title="translate.TR_TITLE_HOME" :subtitle="getProps.folder" />
        <div class="body">
            <ul id="list">
                <li
                    v-for="(item, index) in folder_props"
                    :key="index"
                    class="list-item"
                >
                    <div id="numberBox"><p v-on:click="openScreen('Element', item)" class="numberText">{{index+1}}</p></div>

                    <div id="boxBorder">
                        <div id="infoBox">
                            <p v-on:click="openScreen('Element', item)" class="propText">{{item.prop}}</p>
                            <p v-on:click="openScreen('Element', item)" class="coordsText">x = {{item.pos_x}}, y = {{item.pos_y}}, z = {{item.pos_z}}</p>
                        </div>

                        <div id="statusBox"><p v-on:click="openScreen('Element', item)" class="statusText" :class="{TextEnable: item.status == 1, TextDisabled: item.status == 0}">{{item.status == 1 ? translate.TR_STATUS_ON : translate.TR_STATUS_OFF}}</p></div>
                    </div>
                </li>
            </ul>  
            <div id="buttons-create" class="folder-button">
                <div>  
                    <p v-on:click="activeAll">{{translate.TR_FOLDER_ACTIVE_ALL}}</p>                    
                    <p v-on:click="disableAll">{{translate.TR_FOLDER_DISABLE_ALL}}</p>                    
                    <p v-on:click="deleteAll">{{translate.TR_FOLDER_DELETE_ALL}}</p>                    
                    <p v-on:click="openScreen('Home')">{{translate.TR_ELEMENT_CANCEL}}</p>  
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import Title from '../components/Title';

export default {
    name: "Home",
    props:{
        OpenScreen:{

        },
        getProps:{

        },
        folder_props:{

        },
        translate: {

        },
        NameResource:{

        },
        close:{

        }
    },
    components:{
        Title
    },

    data: function(){
        return({
            
        })
    },

    methods: {
        openScreen: function(screen, props = null){
            this.OpenScreen(screen, props);
            // this.$emit('openScreen', screen, props);
        },

        activeAll: function(){
            fetch(`https://${this.NameResource}/activeAll`, {
                method: 'POST',
                body: JSON.stringify({
                    folder: this.getProps.folder
                })
            });

            this.close();
        },

        disableAll: function(){
            fetch(`https://${this.NameResource}/disableAll`, {
                method: 'POST',
                body: JSON.stringify({
                    folder: this.getProps.folder
                })
            });

            this.close();
        },

        deleteAll: function(){
            fetch(`https://${this.NameResource}/deleteAll`, {
                method: 'POST',
                body: JSON.stringify({
                    folder: this.getProps.folder
                })
            });

            this.close();
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.openFolder){
                this.folder_props = lua.folder_props;
            }
        },


    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
}
</script>

<style>
    .screen{
        position: relative;
    }

    .folder-button{
        margin-top: 0.5vh;
    }
    
</style>