<template>
    <div class="screen">
        <Title :title="translate.TR_TITLE_FOLDERS" :subtitle="translate.TR_SUBTITLE_FOLDERS" />
        <div class="body">  
            <div class="loader" v-if="isLoading"></div>
            <div v-else>
            <div class="options-update"
                v-for="(item,index) in folders"
                :key="(index)"
            ><p v-on:click="openFolder(item.folder)">{{item.folder}}</p></div>
            <div class="options-update"><p v-on:click="cancel">{{translate.TR_ELEMENT_CANCEL}}</p></div>
            </div>
        </div>
    </div>
</template>

<script>

import Title from '../components/Title';

export default {
    name: "Element",
    props:{
        element:{

        },
        getProps:{

        },
        OpenScreen:{

        },
        translate: {
                
        },

        NameResource: {

        },
        close:{
            
        }
    },
    components:{
        Title
    },

    data: function(){
        return({         
            isLoading: true,   
            folders: [],
        })
    },
    methods: {
        openScreen: function(screen, props = null){
            this.OpenScreen(screen, props);
        },

        openFolder: function(folder){
            fetch(`https://${this.NameResource}/openFolder`, {
                method: 'POST',
                body: JSON.stringify({
                    folder: folder
                })
            });

            this.openScreen('Folder', {folder: folder});
        },
        cancel: function(){
            this.OpenScreen('Home');
        },
    
        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.receiveFolders){
                this.isLoading = false;
                this.folders = lua.folders;
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    

        fetch(`https://${this.NameResource}/loadFolders`, {
            method: 'POST',
            body: JSON.stringify({
            })
        });
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
} 
</script>

